import React from 'react';
import { motion } from 'framer-motion';
import SemesterCard from './SemesterCard';
import { Program } from '../data/programData';

interface SemesterSectionProps {
  programData: Program;
}

const SemesterSection: React.FC<SemesterSectionProps> = ({ programData }) => {
  return (
    <section className="py-20 px-4 bg-gradient-to-b from-[#1a2952] to-[#0B1C39]">
      <div className="max-w-7xl mx-auto">
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-5xl font-bold mb-4 text-[#FFD700]">
            {programData.semesterTitle}
          </h2>
          <p className="text-xl text-gray-300 max-w-2xl mx-auto">
            {programData.semesterDescription}
          </p>
        </motion.div>

        <div className={`grid grid-cols-1 md:grid-cols-2 ${
          programData.semesters.length > 4 ? 'lg:grid-cols-3' : 'lg:grid-cols-4'
        } gap-8`}>
          {programData.semesters.map((semester, index) => (
            <motion.div
              key={semester.semester}
              initial={{ opacity: 0, y: 50 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ 
                duration: 0.6,
                delay: index * 0.2
              }}
              viewport={{ once: true }}
            >
              <SemesterCard {...semester} />
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default SemesterSection;